.hamburger {
    width: 50px;
}

.hamburger__line {
    display: block;
    width: 100%;
    height: 5px;
    margin-bottom: 8px;
    background: #000;
    border-radius: 5px;
}

.hamburger__line:last-child {
    margin-bottom: 0;
}

@media screen and (max-width: 856px) {
    .hamburger {
        width: 35px;
    }
}